import * as THREE from 'three';

export function createScene() {
    let renderer, scene, camera, circle, skelet, particle;

    window.onload = function () {
        init();
        animate();
    };

    function init() {
        renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
        renderer.setSize(window.innerWidth, window.innerHeight);
        renderer.autoClear = false;
        renderer.setClearColor(0x000000, 0.0);
        document.getElementById('canvas').appendChild(renderer.domElement);

        scene = new THREE.Scene();

        camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 1, 1000);
        camera.position.z = 400;
        scene.add(camera);

        circle = new THREE.Object3D();
        skelet = new THREE.Object3D();
        particle = new THREE.Object3D();

        scene.add(circle);
        scene.add(skelet);
        scene.add(particle);

        let geometry = new THREE.TetrahedronGeometry(2, 0);
        let geom = new THREE.IcosahedronGeometry(5, 1);
        let geom2 = new THREE.IcosahedronGeometry(12, 1);

        let material = new THREE.MeshPhongMaterial({
            color: 0xffffff,
            flatShading: THREE.FlatShading
        });

        for (let i = 0; i < 1000; i++) {
            let mesh = new THREE.Mesh(geometry, material);
            mesh.position.set(Math.random() - 0.5, Math.random() - 0.5, Math.random() - 0.5).normalize();
            mesh.position.multiplyScalar(90 + (Math.random() * 700));
            mesh.rotation.set(Math.random() * 2, Math.random() * 2, Math.random() * 2);
            particle.add(mesh);
        }

        let mat = new THREE.MeshPhongMaterial({
            color: 0xffffff,
            flatShading: THREE.FlatShading
        });

        let mat2 = new THREE.MeshPhongMaterial({
            color: 0xffffff,
            wireframe: true,
            side: THREE.DoubleSide
        });

        let planet = new THREE.Mesh(geom, mat);
        planet.scale.x = planet.scale.y = planet.scale.z = 16;
        circle.add(planet);

        let planet2 = new THREE.Mesh(geom2, mat2);
        planet2.scale.x = planet2.scale.y = planet2.scale.z = 10;
        skelet.add(planet2);

        let ambientLight = new THREE.AmbientLight(0x999999 );
        scene.add(ambientLight);

        let lights = [];
            lights[0] = new THREE.DirectionalLight( 0xffffff, 1 );
            lights[0].position.set( 1, 0, 0 );
            lights[1] = new THREE.DirectionalLight( 0x11E8BB, 1 );
            lights[1].position.set( 0.75, 1, 0.5 );
            lights[2] = new THREE.DirectionalLight( 0x8200C9, 1 );
            lights[2].position.set( -0.75, -1, 0.5 );
            scene.add( lights[0] );
            scene.add( lights[1] );
            scene.add( lights[2] );

            
        };
    function animate(){
        requestAnimationFrame(animate);
        particle.rotation.x += 0.0005;
        particle.rotation.y -= 0.0010;
        circle.rotation.x -= 0.0010;
        circle.rotation.y -= 0.0010;
        skelet.rotation.x -= 0.0001;
        skelet.rotation.y += 0.0010;
        renderer.clear();
        renderer.render(scene, camera)
    };
    
    window.addEventListener( 'resize', onWindowResize, false );

    function onWindowResize(){
    
        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();
    
        renderer.setSize( window.innerWidth, window.innerHeight );
    
    };
};

